.page {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  scroll-behavior: smooth;

  &__body {
    padding: 0;
    margin: 0;

    &.lock {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: transform 0.3s;

    transform: translateX(-100%);

    &:target {
      transform: translateX(0);
    }
  }

  &__buy {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: transform 0.3s;

    transform: translateX(-100%);

    &:target {
      transform: translateX(0);
    }
  }
}

body {
  @include BgGradient;
}
